/*--------------------首頁開始------------------*/
.top-nav {
	padding: $w-xs 0;
	overflow-x: auto;
    overflow-y: hidden;
	height: 78px;
	white-space: nowrap;
	display: block !important;
	&-item {
		display: inline-block;
		margin-right: $w-sm;
		vertical-align: top;
	}
	&-link {
		display: flex;
		min-width: 100px;
		min-height: 48px;
		justify-content: center;
		align-items: center;
		padding: 0 $w-md;
		background: $white;
		color: $primary-color;
		border: 1px solid $gray200;
		border-radius: $w-xxxs;
		box-shadow: 0 0 9px $gray300;
		transition: all 0 ease;
		position: relative;
        img,p{
            position: relative;
            z-index: 10;
        }
        &:before {
            content: "";
            display: block;
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            background: $white;
            z-index: 5;
            border-radius: $w-xxxs;
            transition: all 0.2s ease;
        }
		&:after {
			content: "";
			display: block;
			position: absolute;
			left: calc(50% - 9px);
			bottom: 10px;
			width: 16px;
			height: 16px;
			background: $white;
			border-radius: 3px;
			transform: rotate(45deg);
			z-index: 1;
			transition: all 0.2s ease;
		}
		&:hover {
			color: $primary-color;
			box-shadow: none;
		}
		&:active {
			border: 1px solid $gray100 !important;
			color: $white !important;
			box-shadow: none;
		}
		&.active {
			background: $primary-color;
			color: $white;
			border: 1px solid $primary-color;
			pointer-events: none;
            &:before{
                background: $primary-color;
            }
			&:after {
				background: $primary-color;
				bottom: -6px;
			}
			img {
				filter: invert(100%) sepia(100%) saturate(0%) hue-rotate(330deg) brightness(300%) contrast(104%);
			}
		}
		&>*{
			position: relative;
			z-index: 2;
		}
	}
	&-icon {
		display: inline-block;
		margin-right: $w-xxs;
		width: 25px;
	}
}
.navbar-turnover{
    border-radius: 0 0 $w-md $w-md;
    background: $gray100;
    position: relative;
    padding: $w-sm $w-md;
    margin-top: -23px;
    &:before,
			&:after {
				content: "";
				display: block;
				position: absolute;
				width: 26px;
				height: 26px;
				top: 0;
				background: url("../images/fillet-gray.svg");
				background-size: 100%;
			}
			&:before {
				left: -24px;
                transform: rotate(-90deg);
			}
			&:after {
				right: -24px;
				transform: rotate(180deg);
			}
}
.menu-card{
    overflow-y: auto;
    height: calc(100vh - 230px);
    &-pic{
        margin-bottom: $w-xxs;
        border-radius: $w-xxxs;
        flex: 0 0;
        img{
            border-radius: $w-xxxs;
            width: 100%;
        }
    }
    &-info{
        flex: 0 0;
        .search-input{
            display: none;
        }
    }
    &-title{
        flex: 1 1;
    }
    li{
        transition: all 0.2s ease;
    }
}
.menu-card-item{
    padding: 0 $w-sm $w-sm $w-sm;
    position: relative;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    color: $gray900 !important;
    height: 100%;
    &:after{
        content:"";
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: calc(100% - #{$w-md});
        background: $gray100;
        border-radius: $w-xxxs;
        transition: all 0.2s ease;
    }
    &:hover{
        &:after{
            background: $white;
        }
    }
    &>*{
        position: relative;
        z-index: 3;
    }
}
.soldout{
    pointer-events: none;
    .menu-card-pic{
        background: $gray900;
        img{
            opacity: 0.5;
        }
        &::before{
        content:"售完";
        display: inline-block;
        position: absolute;
        background: $gray900;
        z-index: 3;
        background: $gray900;
        color: $white;
        padding: $w-xxxs $w-sm;
        font-variation-settings: $font-xlight;
        border-radius: $w-xxxs;
        top: calc(50% - 17px);;
        left: calc(50% - 33px);
    }
    } 
}
.function-link-item{
    display: block;
    opacity: 0.3;
    padding: $w-xxxs;
    img{
        height: 12px;
    }
    &.active{
        opacity: 1;
        pointer-events: none;
    }
}
