html[data-device="pad"] {
    font-size: 1.1rem;
    //側邊主選單
    .sidebar {
        padding: 35px 0 $w-md 0;
        &-logo {
            display: block;
        }
    }
    //上方logo
    .navbar-brand {
        display: none;
    }
    //表單
    .select-list-wrapper .form-check-input {
        width: 20px;
        height: 20px;
    }
    .select-list-wrapper .form-check-input:checked[type="radio"] {
        width: 20px;
        height: 20px;
    }
    //數字搜尋
    .search-input {
        display: none;
    }
    .menu-card-info {
        .search-input {
            display: inline-flex;
            margin-left: $w-sm;
            margin-right: auto;
        }
    }
        //多項目菜單容器
    .menu-wrapper{
        height: calc(100vh - 108px);
        position: relative;
    }
    .menu-card-wrapper{
        width: calc(100% - 208px);
        margin-left: 208px;
        height: 100%;
        .menu-card{
            height: calc(100% - 48px);
        }
    }
    
    //直式菜單分類
    .top-nav[data-align="vertical"] {
        width: 208px;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        margin-right: $w-md;
        margin-left: -8px;
        padding: $w-xxxs $w-md 0 $w-xs;
        overflow-x: hidden;
        overflow-y: auto;
        transition: all 0.2s ease;
        background: $white;
        z-index: 100;
        &.show{
            display: inline-flex !important;
            flex-direction: column;
            align-items: flex-start;
            width: auto;
            white-space:normal;
            overflow: initial;
            padding-right: $w-sm;
            .top-nav-item{
                width: 168px;
                margin-right: $w-sm;
            }
            .top-nav-link::after{
                right: 5px !important;
            }
        }
        //直式菜單伸縮按鈕
     .top-nav-toggle{
            position: fixed;
            left: 100px;
            bottom: 0;
            width: 220px;
            height: 40px;
            padding-right: $w-md;
            background: $white !important;
            z-index: 200;
            border: none !important;
            &.active{
                position: absolute;
                right: 0;
                bottom: -16px;
                left: auto;
                width: 100%;
            }
            img{
                transition: all 0.2s ease;
            }
            button{
                 display: flex;
                 height: 40px;
            justify-content: flex-end;
            align-items: center;
            }
            button.active{
                img{
                    transform: rotate(180deg);
                }
            }
        }
        .top-nav-item {
            margin-bottom: $w-xs;
            margin-right: 0;
            width: 100%;
            display: block;
        }
        .top-nav-link {
            width: 100%;
            padding: 0 $w-sm;
            justify-content: flex-start;
            &:after {
                bottom: calc(50% - 9px);
                left: auto;
                right: 5px;
                transition: all 0.3s ease;
            }
            &.active {
                &:after {
                    right: -7px;
                }
            }
        }
    }
    //用餐方式按鈕
    .top-menu {
        display: inline-flex;
        padding: $w-xxs;
        border-radius: 100px;
        background: rgba($primary-xlight-color, 0.25);
        position: relative;
        &-sub {
            display: none;
        }
        &-link {
            padding: $w-xxs $w-sm;
            border-radius: 50px;
            background: $white;
            color: $primary-color;
            transition: all 0.2s ease;
            cursor: pointer;
            &.active {
                background: $primary-color;
                color: $white;
            }
            &[data-con="dine-in"].active {
                padding-right: $w-xxs;
                .top-menu-sub {
                    display: inline-flex;
                    border-left: 1px solid $primary-xlight-color;
                }
            }
        }
    }
    .btn-dropdown{
        font-size: $font-xxs;
    }
    //多項菜單設定
    .menu-card{
        &[data-num="plenty"]{
            .menu-card-item{
                padding: $w-sm;
                &:after{
                    height: 100% ;
                }
            }
            .menu-card-title{
                letter-spacing: -0.5px;
            }
        }
    }
    //載具表單
    [data-con="carrier"] {
        &::before {
            left: 110px;
        }
    }
}
