/*--------------------歡迎頁開始------------------*/
.landing {
	padding: $w-sm $w-lg;
    background: url("../images/landing-bg.jpg") no-repeat right bottom;
    background-size: cover;
    position: relative;
    width: calc(100vw - 78px);
    height: 100vh;
    &-title{
        position: absolute;
        left: 11%;
        top: 4%;
        transform: rotate(-9deg);
        padding-right: 100px;
        line-height: 1.2;
        @include PC-md{
            left: 12%;
            top: 0.2%;
        }
        &-logo{
            width: 230px;
        }
        &-txt{
            font-size: 90px;
            font-variation-settings: 700;
            color: #3E3A39;
            padding-left: 48px;
            letter-spacing: 3px;
            @include PC-md{
            font-size: 80px;
        }
        }
        &-img{
            position: absolute;
            right: 0;
            bottom: 24px;
            width: 100px;
        }
    }
    &-txt{
        display: inline-flex;
        align-items: center;
        border-radius: $w-md;
        background: $primary-color;
        color: $white;
        position: absolute;
        padding: $w-sm $w-lg;
        transform: rotate(-8deg);
        font-variation-settings: $font-bold;
        font-size: $font-xxl;
        left: 25%;
        top: 52%;
        @include PC-md{
            font-size: $font-xl;
            left: 17%;
            top: 52%;
        }
        &:after{
            content: "";
            display: block;
            position: absolute;
            right: 42px;
            bottom: -20px;
            width: 47px;
            height: 30px;
            background: url("../images/dialogue.svg") no-repeat;
            background-size: cover;
        }
        img{
            width: 40px;
        }
    }
}