.switch {
    display: inline-block;
    position: relative;
    padding: 4px;
    background-color: $gray100;
    width: 200px;
    height: 48px;
    border-radius: 50px;
    font-size: $font-sm;
    box-shadow: inset 0 0px 6px $gray200;
    input {
        position: absolute;
        opacity: 0;
    }
    label {
        position: relative;
        z-index: 2;
        float: left;
        width: 96px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        color: $primary-color;
        cursor: pointer;
        transition: color 0s ease 0.2s;
    }
    input:checked + label {
        color: $white;
    }
    input:focus + label {
        outline: none;
    }
    .switch-button {
        clear: both;
        position: absolute;
        top: 4px;
        left: 4px;
        z-index: 1;
        display: inline-block;
        width: 96px;
        height: 40px;
        background: $primary-color;
        border-radius: 50px;
        transition: all 0.3s ease-out;
    }
    input:last-of-type:checked ~ .switch-button {
        left: 100px;
    }
}
.order-card {
    height: calc(100vh - 232px);
    padding-right: 8px;
    overflow-x: hidden;
    &-wrapper {
        width: 100%;
        margin-bottom: $w-sm;
        display: flex;
        flex-wrap: nowrap;
        align-items: stretch;
        &:nth-last-child(1),
        &:nth-last-child(2) {
            .order-card-function-dropdown {
                bottom: 110px;
                top: auto;
                &:before {
                    top: auto;
                    bottom: 0;
                }
                &.show {
                    &:before {
                        bottom: -6px;
                    }
                }
            }
        }
        &:nth-child(1),
        &:nth-child(2),
        &:nth-child(3) {
            .order-card-function-dropdown {
                top: 110px !important;
                &:before {
                    top: 8px !important;
                }
                &.show {
                    &:before {
                        top: -6px !important;
                    }
                }
            }
        }
    }
    &-item {
        width: 100%;
        display: block;
        padding: $w-sm 22px;
        background: $white;
        color: $primary-color;
        border: 1px solid $gray200;
        border-left: 6px solid $gray600;
        border-radius: $w-xxxs;
        box-shadow: 0 0 6px $gray300;
        transition: all 0.2s ease;
        position: relative;
        flex-shrink: 0;
        &.active {
            flex-shrink: 1;
            border: 1px solid $alert-xlight-color;
            border-left: 6px solid $alert-xlight-color;
            &:before {
                content: " ";
                display: block;
                width: calc(100% + 1px);
                height: 100%;
                border: 1px solid $alert-xlight-color;
                position: absolute;
                border-radius: 0 $w-xxxs $w-xxxs 0;
                left: -1px;
                top: 0;
                pointer-events: none;
            }
            .order-card-tag {
                background: $primary-light-color;
                opacity: 0.6;
            }
        }
    }
    &-header {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &-title {
        font-size: $font-smd;
        font-variation-settings: $font-bold;
    }
    &-tag {
        background: $gray500;
        border-radius: 50px;
        padding: 0 $w-sm;
        height: 27px;
        line-height: 26px;
        color: $white;
        font-size: $font-xs;
    }
    &-info {
        display: flex;
        align-items: center;
        color: $gray900;
        font-size: $font-xs;
        li {
            line-height: 1.2;
            border-right: 1px solid $gray500;
            padding: 0 $w-xxs;
            &:first-child {
                padding-left: 0;
            }
            &:last-child {
                border-right: 0;
            }
        }
    }
    &-price {
        display: flex;
        align-items: center;
        span {
            text-decoration: line-through;
            color: $gray600;
            font-size: $font-xxs;
        }
        p {
            font-size: $font-lg;
            font-variation-settings: $font-heavy;
        }
        div {
            border-radius: 3px;
            font-variation-settings: normal;
        }
    }
    &-body {
        display: flex;
        justify-content: space-between;
        flex: 0 0;
    }
    &-function {
        flex: 0 0;
        color: $white;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-left: $w-xs;
        & > * {
            height: 100%;
            position: relative;
            width: 55px;
            padding: 0;
            border-radius: 0;
            font-size: $font-xsm;
            &:first-child {
                border-top-left-radius: $w-xxxs !important;
                border-bottom-left-radius: $w-xxxs !important;
                border-radius: $w-xxxs 0 0 $w-xxxs;
                button {
                    border-top-left-radius: $w-xxxs !important;
                    border-bottom-left-radius: $w-xxxs !important;
                    border-radius: $w-xxxs 0 0 $w-xxxs;
                }
            }
            &:last-child {
                border-radius: 0 $w-xxxs $w-xxxs 0;
                button {
                    border-radius: 0 $w-xxxs $w-xxxs 0;
                }
            }
        }
        &-dropdown {
            width: 140px;
            position: absolute;
            left: calc(50% - 70px);
            top: 110px;
            z-index: 100;
            &:before {
                content: "";
                position: absolute;
                left: calc(50% - 8px);
                top: 8px;
                width: 16px;
                height: 16px;
                transform: rotate(45deg);
                background: $alert-light-color;
                border-radius: 3px;
                z-index: 0;
                transition: all 0.1s ease;
            }
            &.show {
                &:before {
                    top: -6px;
                }
            }
        }
        &-menu {
            button {
                width: 100%;
                height: 100%;
                padding: 0;
                border-radius: 0;
            }
            ul {
                background: $white;
                border-radius: $w-xxxs;
                border: 2px solid $alert-light-color;
                padding: $w-xxs $w-sm;
                position: relative;
                z-index: 10;
                li:last-child {
                    a {
                        border-bottom: none;
                    }
                }
                a {
                    font-family: "Noto Sans TC", "Microsoft JhengHei", "sans-serif";
                    font-size: $font-sm;
                    color: $gray800;
                    border-bottom: 1px solid $gray400;
                    padding: $w-xxxs;
                    text-align: center;
                    display: block;
                    &:hover {
                        color: $gray900;
                    }
                }
            }
        }
    }
}
.top-nav[data-con="filter"] {
    .top-nav-item {
        display: inline-flex;
        align-items: center;
       margin-right: 8px;
    }
    .top-nav-btn {
        height: 48px;
        padding: 0 $w-sm 0 38px;
        display: flex;
        align-items: center;
        border-radius: 50px;
        box-shadow: 0 0 9px $gray300;
        color: $primary-color;
        border: 1px solid $gray300;
        font-variation-settings: $font-bold;
        position: relative;
        &-icon{
            position: absolute;
            border-radius: 50%;
            background: $white;
            width: 16px;
            height: 16px;
            border: 1px solid $primary-color;
            left: 16px;
            top: 16px;
            transition: all 0.1s ease;
        }
        &.active {
            background: $primary-color;
            color: $white;
            font-variation-settings: $font-light;
            .top-nav-btn-icon{
              border: 4px solid $white;
              background: $primary-color;
            }

        }
    }
}
[data-con="carrier"] {
    position: relative;
    &::before {
        content: "/";
        display: block;
        position: absolute;
        left: 101px;
        top: 5px;
        width: 15px;
        height: 30px;
        color: $gray800;
        z-index: 10;
        font-size: $font-md;
        font-variation-settings: $font-heavy;
    }
    input {
        padding-left: 25px;
    }
}
.offcanvas-body {
    .setting-wrapper {
        padding: 0 32px 24px 32px;
    }
}
.setting-wrapper {
    height: calc(100vh - 99px);
    padding-right: 8px;
    overflow-x: hidden;
    .form-control {
        border: 1px solid $primary-color;
        &[disabled] {
            border: 1px solid $gray500;
        }
        &[readonly] {
            border: 1px solid $gray300;
        }
        &[type="file"] {
            border-radius: 0.375rem !important;
        }
        & ~ button {
            border-radius: 0 0.375rem 0.375rem 0 !important;
        }
    }
    .form-select {
        border: 1px solid $primary-color;
        background-image: url("../images/ic-dropdown.svg");
    }
    .input-group[type="file"] {
        position: relative;
        button {
            position: absolute;
            left: 0;
            top: 0;
            height: 100%;
            width: 91px;
            border-radius: 0.375rem 0 0 0.375rem !important;
            padding-left: 0;
            padding-right: 0;
            z-index: 100;
            pointer-events: none;
        }
    }
    .form-check-input:checked {
        background-color: $primary-color;
        border-color: $primary-color;
    }
    .form-check-input[disabled] {
        background-color: $gray200;
    }
    .form-switch {
        margin-bottom: 10px;
    }
    .form-check-input[role="switch"] {
        height: 30px;
        width: 55px;
        margin-top: -2px;
        margin-right: 8px;
        background-color: $gray300;
        background-image: url("../images/ic-switch.svg");
        background-position: -3px -4px;
        background-size: 38px;
        border: none;
        &:checked {
            background-color: $primary-color !important;
            background-position: 20px -4px;
        }
    }
}
#date-inline-picker {
    thead {
        th[title="Select Month"] {
            height: 54px;
            vertical-align: middle;
        }
        tr:last-child {
            background: $primary-color;
            th {
                border-radius: 0;
                color: $white;
                padding-top: 6px;
                padding-bottom: 8px;
                font-weight: $font-light;
                &:first-child {
                    border-radius: 5px 0 0 5px;
                }
                &:last-child {
                    border-radius: 0 5px 5px 0;
                }
            }
        }
    }
    tbody {
        tr {
            border-bottom: 1px solid $gray300;
            &:last-child {
                border-bottom: none;
            }
            p {
                position: relative;
                z-index: 1;
            }
        }
    }
}
.input-group-text {
    box-shadow: none !important;
    border: none !important;
    &:active {
        box-shadow: none !important;
        border: none !important;
    }
}

.input-group.focus{
    span{
        background: $alert-color !important;
    }
    .form-control{
        border-color: $alert-color;
    }
}