body {
	font-family:
		Noto Sans TC,
		微軟正黑體;
}
.blockMsg {
	border: 0 !important;
	width: 100px !important;
	height: 100px !important;
	top: calc(50% - 50px) !important;
	left: calc(50% - 50px) !important;
	display: flex !important;
	justify-content: center !important;
	align-items: center !important;
	border-radius: $w-md !important;
}
/*--------------------顧客端畫面開始------------------*/
[data-con="consumer"] {
	.content {
		width: calc(100% - 430px);
		display: flex;
		justify-content: center;
		align-items: center;
		max-height: 100vh;
		padding: 0;
		margin-right: -20px;
		&-video {
			z-index: 0;
			flex-shrink: 1;
			width: 100%;
			background: $black;
			height: calc(100% - 240px);
			&[data-con="videoonly"] {
				height: 100%;
			}
			video {
				width: 100%;
				height: 100%;
			}
		}
		&-img {
			width: 100%;
			height: 100%;
		}
		&-banner {
			width: 100%;
			height: 240px;
			flex-shrink: 0;
		}
	}
	.sidelist {
		width: 450px;
		position: relative;
		z-index: 10;
	}
	.sidebar {
		display: none;
	}
	.sidelist-header {
		background: $primary-color;
		margin-bottom: $w-sm;
		border-radius: $w-md 0 0 0;
		padding: $w-xxs $w-md !important;
		button {
			color: $white !important;
			img {
				filter: brightness(1000%);
			}
		}
	}
	.price-card {
		padding: $w-xs $w-lg $w-sm $w-lg !important;
	}
}
/*--------------------主選單開始------------------*/
.sidebar {
	width: 78px;
	height: 100vh;
	position: relative;
	padding: 56px 0 $w-md 0;
	display: flex;
	flex-direction: column;
	flex-shrink: 0;
	&:before,
	&:after {
		content: "";
		display: block;
		position: absolute;
		width: 24px;
		height: 24px;
		right: -24px;
		background: url("../images/fillet-dark.svg");
		background-size: 100%;
		z-index: 100;
	}
	&:before {
		top: 0;
	}
	&:after {
		bottom: 0;
		transform: rotate(-90deg);
	}
	&-nav {
		flex-shrink: 1;
	}
	&-btn {
		flex-direction: column;
		width: calc(100% - 16px);
		margin-left: $w-xxs;
		padding: $w-xs 0;
		border-radius: $w-md;
	}
	&-logo {
		display: none;
		width: 65%;
		margin: 0 auto;
	}
}
.nav {
	position: relative;
	&-item{
		position: relative;
		z-index: 100;
	}
	&-link {
		display: flex;
		flex-direction: column;
		align-items: center;
		color: $white;
		padding: $w-md 0;
		font-variation-settings: $font-bold;
		line-height: 1.2;
		transition: all 0.2s ease;
		&.active {
			margin-left: 8px;
			margin-right: -1px;
			color: $primary-color !important;
			position: relative;
			
			img {
				filter: invert(45%) sepia(88%) saturate(776%) hue-rotate(173deg) brightness(84%) contrast(90%);
			}
		}
		&:hover,
		&:active,
		&:focus {
			color: $white;
		}
		img {
			width: 28px;
			margin-bottom: 4px;

		}
	}
	&-active{
		border-radius: $w-md 0 0 $w-md;
		background: $white !important;
		position: absolute;
		right: 0;
		top: 10px;
		width: 71px;
		transition: all 0.2s ease;
		z-index: 1;
		&:before,
			&:after {
				content: "";
				display: block;
				position: absolute;
				width: 26px;
				height: 26px;
				right: -2px;
				background: url("../images/fillet-light.svg");
				background-size: 100%;
			}
			&:before {
				top: -24px;
			}
			&:after {
				bottom: -24px;
				transform: rotate(-90deg);
			}
	}
}
/*--------------------內容開始------------------*/
.navbar {
	display: flex;
	justify-content: space-between;
	flex-wrap: nowrap;
	align-items: center;
	&-brand {
		width: 228px;
		h1 {
			font-size: $font-xxl;
		}
	}
	&-info {
		display: flex;
		align-items: center;
		font-variation-settings: $font-bold;
		img {
			width: 20px;
		}
	}
}

.search-input {
	width: 280px;
	position: relative;
	z-index: 200;
	input {
		border-radius: $w-xxxs 0 0 $w-xxxs;
		background: $gray100 !important;
		border: none;
		height: 45px;
		&:focus {
			box-shadow: none;
		}
	}
	input ~ button {
		border-radius: 0 $w-xxxs $w-xxxs 0 !important;
		background: $gray100 !important;
		border: none;
		height: 45px;
		&:hover {
			img {
				opacity: 0.8;
			}
		}
	}
}

.search-dropdown {
	position: absolute;
	left: 0;
	top: -500px;
	border-radius: $w-xxxs !important;
	box-shadow: 0 0 9px $gray400;
	z-index: 5;
	padding: $w-md $w-md $w-xxs $w-md;
	width: 230px;
	border: 1px solid $gray300;
	transition: all 0.2s ease;
	z-index: 200;
	&.active {
		top: 60px;
	}
	&-item {
		width: 50px;
		height: 50px;
		border-radius: 50% !important;
		background: $gray100;
		padding: 0;
		color: $gray900 !important;
		margin-bottom: $w-sm;
		border: 1px solid $gray100 !important;
		font-size: $font-smd;
		display: flex;
		justify-content: center;
		align-items: center;
		&:hover,
		&:active {
			background: $gray200;
		}
		&[data-con="clear"] {
			background: $white !important;
			font-size: $font-xxs;
			color: $gray800 !important;
		}
	}
}
.content-wrapper{
	display: flex;
}
.content {
	padding: $w-sm $w-lg;
	width: calc(100vw - 373px - 78px);
	&[data-con="nodata"] {
		width: calc(100vw - 78px);
	}
}

/*--------------------訂單清單開始------------------*/
.sidelist {
	width: 373px;
	border-radius: $w-md 0 0 $w-md;
	height: 100vh;
	display: flex;
	flex-direction: column;
	flex-shrink: 0;
	&-header {
		flex: 0 0;
		padding: $w-md $w-lg;
	}
}

.sidelist-nav {
	display: block;
	overflow-x: auto;
	height: 36px;
	white-space: nowrap;
	&-item {
		display: inline-block;
		margin-right: 11px;
		vertical-align: top;
		width: calc((100% - calc(#{$w-sm} * 2)) / 3);
		&:last-child {
			margin-right: 0;
		}
	}
	&-link {
		background: $gray200;
		color: $gray900;
		border-radius: 50px;
		width: 100%;
		border: none;
		&.active {
			background: $gray800;
			color: $white;
		}
	}
}
.order-list-wrapper {
	cursor: pointer;
	overflow: auto;
	flex: 1 1;
	padding: 0 $w-lg;
}
.order-list {
	overflow: hidden;
	width: 100%;
	&-item {
		display: flex;
		position: relative;
		left: 0;
		padding: 0 0 $w-sm 0;
		margin-bottom: $w-sm;
		&:after {
			content: " ";
			display: block;
			width: calc(100% + 82px);
			height: 1px;
			background: $gray300;
			position: absolute;
			left: 0;
			bottom: 0;
		}
		&:last-child {
			margin-bottom: 0;
			&:after {
				display: none;
			}
		}
	}
	&-end {
		width: 100px;
		flex-shrink: 0;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: flex-end;
	}
	&-img {
		width: 100%;
		border-radius: $w-xxxs;
	}
	&-info {
		width: 100%;
		button[data-style="action"] {
			width: 46px;
			height: 30px;
			padding-left: 0;
			padding-right: 0;
			img {
				width: 20px;
				margin: 0;
			}
		}
	}
	&-title {
		font-size: $font-sm;
		margin-bottom: 4px;
	}
	&-caption {
		font-variation-settings: $font-bold;
	}
	&-txt {
		font-size: $font-xxs;
		color: $gray700;
	}
	&-remove {
		width: 66px;
		background: $danger-color;
		position: absolute;
		top: 0;
		right: -82px;
		color: $white;
		display: flex;
		justify-content: center;
		align-items: center;
		height: calc(100% - #{$w-sm});
	}
}
.number-form {
	display: flex;
	align-items: center;
	justify-content: space-between;
	flex-wrap: nowrap;
	width: 110px;
	button {
		border-radius: $w-xxxs !important;
		padding: 0;
		width: 30px;
		height: 30px;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-shrink: 0;
	}
	div {
		width: 100%;
		cursor: pointer;
		line-height: 30px;
		padding: 0 $w-xxxs;
	}
}
.price-card {
	font-variation-settings: $font-bold;
	font-size: $font-xs;
	padding: $w-sm $w-lg $w-md $w-lg;
	position: relative;
	flex: 0 0;
	&:before {
		pointer-events: none;
		content: "";
		display: block;
		position: absolute;
		left: 0;
		top: -30px;
		width: 100%;
		height: 30px;
		background: url("../images/scroll-shadow.svg") repeat-x;
	}
	&-item {
		display: flex;
		align-items: center;
		justify-content: space-between;
		&[data-con="total"] {
			border-top: 1px solid $gray300;
			margin-top: $w-xs;
			padding-top: $w-xxxs;
		}
	}
	&-action {
		display: flex;
		justify-content: space-between;
		button {
			width: 100%;
			font-size: $font-md;
			padding: $w-xxs 0;
			border-radius: $w-xxxs !important;
		}
	}
	&-btn {
		display: flex;
		align-items: center;
		color: $primary-color !important;
		font-size: $font-xs;
		font-variation-settings: $font-bold;
		padding: 0;
		border: 0 !important;
	}
}
.price-card-checked {
	padding-bottom: $w-xs;
	.price-card-item {
		&:last-child {
			color: $white !important;
			position: relative;
			p {
				position: relative;
				z-index: 2;
				font-variation-settings: $font-light;
			}
			&::after {
				content: "";
				display: block;
				width: calc(100% + (#{$w-lg} * 2));
				height: calc(100% + #{$w-xs});
				position: absolute;
				left: -32px;
				top: 0;
				background: $primary-color;
				border-radius: 0 0 0 $w-md;
				z-index: 0;
			}
		}
	}
}
.box-list-wrapper {
	flex: 1 1;
	overflow-y: auto;
	padding: 0 $w-lg;
}
.box-list {
	&-item {
		border-radius: $w-xxxs;
		background: $white;
		color: $primary-color;
		width: 100%;
		height: 100%;
		min-height: 80px;
		padding: $w-xs;
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		align-items: center;
		&:hover,
		&:focus,
		&:first-child:active {
			background: $white;
			color: $primary-color;
		}
		&.active {
			background: $alert-light-color;
			border: 2px solid $white;
			color: $white;
			&:before {
				content: "已選";
				font-size: $font-xxs;
				padding-left: $w-sm;
				color: $white !important;
				background: url("../images/ic-check.svg") no-repeat left center;
			}
		}
		&.disable {
			background: $primary-dark-color;
			color: $white;
			pointer-events: none;
		}
	}
	&-number {
		font-variation-settings: $font-heavy;
		font-size: $font-md;
	}
	&-text {
		font-variation-settings: $font-bold;
	}
	&-action {
		flex: 0 0;
		padding: 0 $w-lg;
		button {
			font-size: $font-smd;
			border: none;
		}
	}
}
.box-list[data-con="text"] {
	.box-list-item {
		min-height: 54px;
		padding: 0 $w-sm;
		flex-direction: row;
		justify-content: flex-start;
		font-variation-settings: $font-bold;
		position: relative;
		border: 2px solid $white;
		&.active {
			background: $alert-light-color;
			color: $white;
			font-variation-settings: $font-light;
			&:before {
				content: "";
				width: 16px;
				height: 16px;
				display: inline-block;
				background: url("../images/ic-check.svg") no-repeat left center;
			}
		}
		img {
			height: 100%;
			position: absolute;
			right: 0;
			top: 0;
		}
	}
}
.box-list[data-con="number"] {
	.box-list-item {
		min-height: 54px;
		justify-content: center;
		font-variation-settings: $font-bold;
		font-size: $font-md;
		&[data-con="clear"] {
			background: $primary-xlight-color;
			font-size: $font-sm;
		}
		&.active {
			background: $alert-light-color;
			color: $white;
			font-variation-settings: $font-light;
		}
	}
	.box-list-form {
		background: $primary-dark-color !important;
		color: $white !important;
		font-size: $font-lg;
		font-variation-settings: $font-heavy;
		border: 0;
		height: 45px;
		text-align: center;
		letter-spacing: 2px;
		caret-color: transparent;
		&::placeholder {
			color: darken($color: $primary-xlight-color, $amount: 15%) !important;
			font-variation-settings: $font-light;
			letter-spacing: 1px;
		}
	}
	.box-list-form {
		&:-webkit-autofill,
		&:-webkit-autofill:hover,
		&:-webkit-autofill:focus,
		&:-webkit-autofill:active {
			transition: background-color 5000s ease-out 0.5s !important;
			-webkit-text-fill-color: $white !important;
		}
	}
}
.box-list[data-con="number"][data-style="member"]{
	.box-list-form {
		text-align: left;
	}
	.box-list-item{
		min-height: initial;
		height: 32px;
	}
}
/*--------------------視窗開始------------------*/
.backdrop {
	width: 100vw;
	height: 100vh;
	position: absolute;
	left: 0;
	top: 0;
	display: none;
	z-index: 100;
	&.show {
		display: block;
	}
}
.offcanvas-backdrop.show {
	opacity: 0.2;
}
.offcanvas-header {
	padding-left: 20px;
	padding-right: 32px;
	.btn {
		&:active,
		&:focus {
			border: 0;
		}
	}
}
.offcanvas-bottom {
	border-radius: $w-md $w-md 0 0;
	height: 412px !important;
	padding: $w-md 0;
	.offcanvas-header {
		position: relative;
		&-close {
			position: absolute;
			background: $gray100 !important;
			border-radius: $w-md $w-md 0 0;
			width: 106px;
			height: 40px;
			top: -39px;
			left: calc(50% - 53px);
			display: flex;
			justify-content: center;
			align-items: center;
			border: none;
			&::before,
			&::after {
				content: "";
				display: block;
				position: absolute;
				width: 24px;
				height: 24px;
				background: url("../images/fillet-gray.svg");
			}
			&::before {
				bottom: -1px;
				left: -23px;
			}
			&::after {
				bottom: -1px;
				right: -23px;
				transform: rotate(90deg);
			}
			&:focus {
				background: $gray100;
			}
		}
	}
	.offcanvas-body {
		padding: 0 66px;
		overflow-y: initial;
	}
}
.offcanvas-end {
	width: 373px !important;
	border-radius: $w-md 0 0 $w-md;
	&-lg {
		width: 535px !important;
	}
	.offcanvas-header {
		border-radius: $w-md 0 0 0;
	}
}
.offcanvas-body {
	padding: 0 $w-lg $w-md $w-lg;
}
.select-list {
	&-btn {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: space-between;
		width: 100%;
		height: 100%;
		line-height: 1.2;
		padding: $w-xs $w-xs;
		background: $white;
		border: 1px solid $gray200 !important;
		border-radius: $w-xxxs;
		box-shadow: 0 0 9px $gray300;
		transition: all 0 ease;
		position: relative;
		font-size: $font-xsm;
		overflow: hidden;
		&::before {
			background: $white;
			content: " ";
			width: 100%;
			height: 100%;
			position: absolute;
			left: 0;
			top: 0;
			z-index: 0;
			transition: all 0.1s ease;
		}
		&:hover {
			background: $white;
			box-shadow: none;
		}
		&.active {
			background: $primary-color;
			color: $white;
			border: 1px solid $primary-color;
			font-variation-settings: $font-light;
			&::before {
				background: $primary-color;
			}
			p {
				color: $white;
			}
			span {
				color: $white;
				opacity: 0.7;
				&.soldout {
					color: $shine-color;
					opacity: 1;
				}
			}
		}
		&[data-status="soldout"] {
			pointer-events: none;
			box-shadow: none;
			span {
				color: $danger-color;
			}
		}
		p {
			margin-bottom: 4px;
			position: relative;
			z-index: 10;
			color: $gray900;
			font-variation-settings: $font-bold;
		}
		span {
			font-size: $font-xs;
			color: $primary-color;
			position: relative;
			z-index: 10;
			transition: all 0.1s ease;
		}
	}
}

.select-list-wrapper {
	overflow-y: auto;
	padding: 0 $w-lg $w-md $w-lg;
	flex: 1 1;
	font-size: $font-xxs;
	.form-check-input {
		width: $w-sm;
		height: $w-sm;
		margin-top: 0;
		border: 2px solid $gray700 !important;
		&[type="radio"] {
			border-radius: 50%;
		}
		&:checked[type="radio"] {
			border-radius: 50%;
			background: url(../images/radio-circle.svg) no-repeat center $primary-color;
			background-size: 6px;
			border: 2px solid $primary-color !important;
		}
		&[type="checkbox"] {
			border-radius: $w-xxxs;
		}
		&:checked[type="checkbox"] {
			background: url(../images/ic-check.svg) no-repeat center $primary-color;
			background-size: 9px;
			border: 2px solid $primary-color !important;
		}
	}
	input[disabled] {
		border: 2px solid $gray600;
	}
	input[disabled] ~ label {
		opacity: 1 !important;
		color: $gray700;
	}
	input[disabled] ~ label span {
		color: $danger-color !important;
	}
	label {
		display: flex;
		justify-content: space-between;
		padding-left: $w-xxxs;
		p {
			display: block;
			width: calc(100% - 70px);
			flex: 1 1;
			line-height: 1.3;
		}
		span {
			padding-left: $w-xxs;
		}
	}
}
.modal {
	&-content {
		background: $primary-color;
		border-radius: $w-md;
		overflow: hidden;
	}
	&-header {
		border: 0;
		color: $white;
		display: flex;
		justify-content: center;
		position: relative;
		button {
			position: absolute;
			right: 10px;
			&:focus,
			&:active {
				border: none !important;
			}
		}
	}
	&-body {
		background: $white;
		font-size: $font-smd;
		min-height: 100px;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	&-footer {
		background: $white;
		border: 0;
		display: flex;
		justify-content: center;
		& > * {
			width: 130px;
			margin: 0;
			flex-shrink: 1;
		}
	}
	&-action {
		border-radius: $w-xxxs;
		letter-spacing: 2px;
		max-width: 200px;
	}
}
.checkout {
	display: flex;
	justify-content: space-between;
	align-items: stretch;
	height: 100%;
	overflow: hidden;
	padding: 8px 0;
	&[data-con="has-receipt"] {
		.checkout-receipt {
			display: block;
		}
		.checkout-sum-item {
			p:first-child {
				font-size: $font-sm !important;
			}
			p:last-child {
				font-size: $font-lg !important;
			}
		}
		.checkout-sum-list {
			padding-bottom: 0;
		}
	}
	&-pay {
		width: 241px;
		flex-shrink: 0;
		margin-right: $w-md;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		height: 100%;
		li {
			height: calc(33.33% - 6px);
		}
		&-item {
			display: flex;
			height: 100%;
			align-items: center;
			padding: 0 $w-md;
			background: $white;
			color: $primary-color;
			border-radius: $w-xxxs;
			box-shadow: 0 0 9px $gray300;
			transition: all 0.2s ease;
			position: relative;
			font-size: $font-smd;
			border: 1px solid $gray300;
			&:hover {
				color: $primary-color;
				box-shadow: none;
			}
			&:after {
				content: "";
				display: block;
				position: absolute;
				right: 10px;
				bottom: calc(50% - 9px);
				width: 16px;
				height: 16px;
				background: $white;
				border-radius: 3px;
				transform: rotate(45deg);
				z-index: 1;
				transition: all 0.2s ease;
			}
			&.active {
				background: $primary-color;
				color: $white;
				border: 1px solid $primary-color;
				&:after {
					background: $primary-color;
					right: -6px;
				}
				img {
					filter: invert(100%) sepia(100%) saturate(0%) hue-rotate(330deg) brightness(300%) contrast(104%);
				}
			}
		}
	}
	&-coupon {
		width: 86px;
		margin-right: 20px;
		flex-shrink: 0;
		position: relative;
		transition: all 0.2s ease;
		overflow: hidden;
		&.active {
			width: 442px;
			margin-right: 12px;
		}
		&.hide {
			display: none;
		}
		&-toggler {
			width: 86px;
			height: 100%;
			margin-right: 24px;
			display: flex;
			flex-direction: column;
			align-items: center;
			font-size: $font-smd;
			line-height: 1.2;
			position: relative;
			border-radius: $w-xxxs;
			z-index: 10;
			&:after {
				content: "";
				display: block;
				position: absolute;
				right: 2px;
				bottom: calc(50% - 9px);
				width: 16px;
				height: 16px;
				border-radius: 3px;
				transform: rotate(45deg);
				z-index: 1;
				transition: all 0.2s ease;
				background: $danger-light-color;
			}
			&:hover {
				&:after {
					background: darken($color: $danger-light-color, $amount: 5%);
				}
			}
			&.active,
			&:focus {
				background: $danger-light-color !important;
				&:after {
					background: $danger-light-color;
					right: -6px;
				}
			}
			&.using {
				background: $white !important;
				border: 2px solid $danger-light-color;
				color: $danger-light-color !important;
				&:after {
					background: $white;
					right: 2px;
				}
				img {
					filter: none;
				}
			}
			img {
				filter: invert(100%) sepia(70%) saturate(0%) hue-rotate(216deg) brightness(500%) contrast(100%);
				margin-bottom: 4px;
			}
		}
		&-list {
			position: absolute;
			width: 340px;
			height: calc(100% + 8px);
			left: -373px;
			padding: 8px 8px 0 8px;
			top: -8px;
			z-index: 0;
			overflow: auto;
			justify-content: flex-start;
			flex-direction: column;
			flex-wrap: nowrap;
			&.active {
				left: auto;
				right: 0;
			}
			&-item {
				width: 100%;
			}
			&-select {
				width: 100%;
				height: 70px;
				display: flex;
				justify-content: space-between;
				align-items: center;
				margin-bottom: $w-xs;
				border-radius: $w-xxxs;
				box-shadow: 0 0 9px $gray300;
				border: 1px solid $gray300;
				transition: all 0.2s ease;
				text-align: left;
				font-size: $font-sm;
				padding: 0 $w-xs 0 $w-sm;
				background: $white;
				&:active,
				&.active {
					background: $white;
					border: 2px solid $danger-light-color;
					color: $danger-light-color;
					transition: all 0.2s ease;
				}
				&.active {
					button {
						display: block;
					}
				}
			}
			&-btn {
				height: 44px;
				font-size: $font-sm;
				display: none;
			}
		}
	}
	&-keyboard {
		width: 510px;
		margin-right: 20px;
		height: 100%;
		flex-shrink: 0;
		&-list {
			margin: 0 auto;
			&[data-con="pay-service"] {
				.checkout-keyboard-list-item {
					background: $white;
					&:hover {
						box-shadow: none;
					}
				}
			}
			&-item {
				width: 100%;
				height: 100%;
				font-size: $font-xl;
				box-shadow: 0 3px 8px $gray400;
				border: 0;
				&:hover {
					box-shadow: none;
				}
			}
		}
		&-form {
			width: 100%;
			max-width: 520px;
			margin: 0 auto;
			padding: 0 3px;
			&-input {
				text-align: center;
				color: $gray800;
				font-variation-settings: $font-heavy;
				font-size: $font-xl;
				letter-spacing: 3px;
				position: relative;
				border-radius: $w-xxxs;
			}
			&-label {
				position: absolute;
				left: $w-md;
				top: 0;
				color: $gray700;
				z-index: 10;
				height: 56px;
				line-height: 56px;
				font-size: $font-smd;
				font-variation-settings: $font-bold;
				background: none;
			}
		}
	}
	&-end {
		display: flex;
		flex-direction: column;
		flex-shrink: 0;
		width: 315px;
	}
	&-receipt {
		display: none;
		flex-shrink: 0;
	}
	&-sum {
		display: flex;
		flex-direction: column;
		height: 100%;
		&-list {
			display: flex;
			justify-content: space-between;
			flex-direction: column;
			flex-grow: 1;
			padding-bottom: 12px;
		}
		&-item {
			display: flex;
			align-items: center;
			justify-content: space-between;
			&:last-child {
				margin-bottom: 0;
			}
		}
		&-unlock {
			flex-shrink: 0;
		}
	}
}
/*右滑結帳*/
.slidetounlock {
	width: 100%;
	border-radius: $w-xxxs;
	height: 60px;
	background: $gray700;
	box-shadow: inset 0 3px 6px $gray800;
	overflow: hidden;
	-webkit-user-select: none;
	&-item {
		display: flex;
		align-items: center;
		font-size: $font-md;
		color: $white;
		-webkit-text-size-adjust: none;
		padding: $w-xxs;
		height: 100%;
	}
	&-slider {
		max-width: 67px;
		height: 100%;
		box-shadow: 0 3px 6px $gray900;
		transition: all 0.1s ease;
		border-radius: $w-xxxs;
		img {
			width: 100%;
		}
	}
	&-txt {
		padding-left: $w-sm;
	}
}

.btn-dropdown {
	border-radius: 80px;
