	font-variation-settings: $font-bold;
	font-size: $font-xs; /*字體縮小*/
	&:hover,
	&:focus {
		background-color: $white;
	}
	&[data-con="foodpanda"] {
		color: $white !important;
		font-variation-settings: $font-light;
		background-image: url("../images/ic-down-arrow-light.svg");
		background-repeat: no-repeat;
		background-position: right center;
		background-color: $foodpanda-color !important;
		&[data-status="disabled"] {
			color: $foodpanda-color !important;
