	}
	&[data-con="ubereat"] {
		color: $white !important;
		font-variation-settings: $font-light;
		background-image: url("../images/ic-down-arrow-light.svg");
		background-repeat: no-repeat;
		background-position: right center;
		background-color: $ubereat-color !important;
		&[data-status="disabled"] {
			color: $ubereat-color !important;
