//會員功能
.member-search{
	width: 305px;
	height: 305px;
	flex-shrink: 0;
	border-radius: 50%;
	background: $white;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	img{
		width: 100px;
	}
	.btn{
		width: 135px;
	}
}
.dots::after {
    content: "";
	width: 30px;
    display: inline-block;
	text-align: left;
    animation: dots 1.5s infinite;
  }

  @keyframes dots {
    0% {
      content: "";
    }
    33% {
      content: ".";
    }
    66% {
      content: "..";
    }
    100% {
      content: "...";
    }
  }
.member-pick{
	border-radius: 5px;
	padding: -16px;
	display: flex;
	align-items: center;
	background: $white !important;
