	font-weight: $font-bold;
	border: 2px solid $white;
	color: $gray800 !important;
	&-con{
		display: flex;
		flex-direction: column;
		align-items: flex-start;
	}
	&-tag{
		background: $gray600;
		color: $white !important;
		display: inline-block;
		border-radius: 50px;
		padding: 0 12px;
		font-size: $font-xxs;
	}
	&.active{
		background: $alert-light-color !important;
